/* styles.css */

.productos-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; /* Distribuir las tarjetas horizontalmente */
    max-width: 1200px; /* Ancho máximo del contenedor */
    margin: 0 auto; /* Centrar el contenedor en la página */
  }
  
  .producto-card {
    flex: 0 0 calc(20% - 10px); /* Acomodar en 5 columnas con espacio entre ellas */
    margin-bottom: 20px; /* Espacio inferior entre las tarjetas */
    border: 1px solid #ccc; /* Borde para las tarjetas */
    border-radius: 8px; /* Borde redondeado */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Sombra */
    padding: 10px;
    background-color: #fff;
  }
  
