.App {
  text-align: center;
  background-color: rgb(132, 0, 255);
  background: rgb(2, 0, 36);
  background: linear-gradient(90deg, rgb(22, 18, 107) 0%, rgba(82, 9, 121, 0.8155637254901961) 67%, rgba(0, 212, 255, 1) 100%);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: rgb(0, 119, 255);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(155, 19, 19);
  background: rgb(2, 0, 36);
  background: linear-gradient(90deg, rgba(2, 0, 36, 1) 0%, rgba(9, 11, 121, 0.816) 67%, rgb(30, 255, 0) 100%);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
  
  
}